import React from "react"
import {
  Button,
  Col,
  Row
} from "react-bootstrap";

const Opcion=({opcion,remove,idp,ids,idg})=>{
      return (
        <div>
        <Row style={{padding: "0.1rem"}}>
        <Col>
           {opcion.nombreOpcion}</Col>
        <Col>
          Precio Adicional: {opcion.precioAdicional}
        </Col>
        <Col>
  
           <Button variant="danger" size="sm"
                onClick={()=>{remove("opcion",ids,idp,idg,opcion.ido)}}>
               Borrar
             </Button> 
             </Col>
        </Row>
        </div>)
   
}
export default Opcion;