import React,{useState} from "react"
import Producto from './Producto.js'
import {
  Card,
  Button,
  Modal,
  FormGroup,
  Row,
  Col,
  Form
} from "react-bootstrap";


const Seccion=({seccion,remove,edit,add,copy,paste})=>{
  const [current, setChange] = useState("");
  const [mostrarProductos,setProductos]=useState(false)
  const [modal,mostrarModal]=useState(false)
  const [nuevo, setNuevo]=useState("")
  const handleNuevo=(e)=>{
    e.preventDefault()
    const {value, name} =e.target 
    if(value!==""){
    setNuevo({...nuevo, [name] : value})}}

  const handleInput=(e)=>{
    e.preventDefault()
    const {value, name} =e.target 
    if(value!==""){
    setChange({
      [name]: value})}}

const handleSubmit=(id)=>{
      const idp=Math.floor(Math.random() * 10000) + 1
      const nuevoproducto={idp:idp,nombreProducto: nuevo.nombreProducto, descripcion: nuevo.descripcion, precio:nuevo.precio , grupoOpciones:[]}
      add("producto",nuevoproducto,id)}  

return (
  
  <Card>
  <Card.Header>
    <div>
    <Row>
  <Col><h4 className="col">{seccion.nombreSeccion}</h4></Col>               
  <Col style={{textAlign: "rigth"}}>
  <Button variant="primary" onClick={()=>{mostrarModal(!modal);setChange({nombreSeccion:seccion.nombreSeccion})}}>Editar</Button> {' '}
  <Button  variant="danger"onClick={()=>remove("seccion",seccion.ids)}>Borrar</Button> {' '}</Col> 
  <Button size="sm" variant="light" onClick={()=>setProductos(!mostrarProductos)}>{mostrarProductos? "Ocultar Productos": "Mostrar Productos"}</Button> 
  </Row>
  </div>
  </Card.Header>
  <Modal show={modal} >
  <Modal.Header>
   <div><h3>Cambiar Sección</h3></div>
  </Modal.Header>
  <Modal.Body >
    <FormGroup>
      <label>
        Nombre seccion: 
      </label>
      <input
        className="form-control"
        name="nombreSeccion"
        type="text"
        onChange={handleInput}
        placeholder={current.nombreSeccion}
        defaultValue={current.nombreSeccion}
      />
    </FormGroup>
  </Modal.Body>
  <Modal.Footer>
    <Button
      variant="success"
      onClick={()=>{mostrarModal(!modal);edit("seccion",current.nombreSeccion,seccion.ids)}}
    >
      Editar
    </Button>
    <Button
      variant="danger"
      onClick={()=>mostrarModal(!modal)}
  
    >
      Cancelar
    </Button>
  </Modal.Footer>
</Modal>       
{mostrarProductos &&
(<Card.Body>
   {seccion.productos.map((producto)=> {return <Producto  key={producto.idp} producto={producto} edit={edit} 
    remove={remove} ids={seccion.ids} add={add} copy={copy} paste={paste}/>})}
  
<Form.Group style={{padding: "1rem"}}>
<Row  >
<Col><input type="text" required onChange={handleNuevo} name="nombreProducto" className="form-control" placeholder="Nombre de Producto"></input></Col>
<Col><input type="number" required min="0" onChange={handleNuevo} name="precio" className="form-control" placeholder="Precio"></input></Col>
</Row>
<Row style={{padding: "1rem"}} >
<input type="text" required onChange={handleNuevo} name="descripcion" className="form-control" placeholder="Descripción de Producto"></input>
</Row>
<Button type="button"  variant="success" onClick={()=>{handleSubmit(seccion.ids)}}>Agregar Nuevo Producto</Button> 
</Form.Group>
</Card.Body> )}
    
 
    </Card>
   
  )     


}
export default Seccion;