import React, { useState} from "react"
import {
  Card,
  Modal,
  Col,
  FormGroup,
  Form,
  Row,
  Button
} from "react-bootstrap";
import Opcion from "./Opcion"


const GrupoOpciones =({grupo,add,remove,edit,copy,paste,ids,idp})=>{
const[modal,setModal]=useState(false)
const[mostrarOpciones,setOpciones]=useState(true)
const [current, setChange] = useState("");
const [nuevo, setNuevo]=useState("")
    const handleNuevo=(e)=>{
      e.preventDefault()
      const {value, name} =e.target 
      if(value!==""){
      setNuevo({...nuevo, [name] : value})}}

const handleInput=(e)=>{
    e.preventDefault()   
    const {value, name} =e.target 
    if(value!==""){
    setChange({...current, [name] : value})
}}
const handleSubmit=(ids,idp,idg)=>{
      const ido=Math.floor(Math.random() * 10000) + 1
      let nuevaopcion={ido:ido, nombreOpcion: nuevo.nombreOpcion, precioAdicional: nuevo.precioAdiconal}
      
      add("opcion",nuevaopcion,ids,idp,idg)}
    
return(
 <div style={{padding: "0.2rem"}}>
   <div>
   <Row>
 <Col><h5 className="col">{grupo.nombreGrupo}</h5></Col>
 <Col ><h5 className="col">Min: {grupo.cantidadMinima}  Max: {grupo.cantidadMaxima}</h5></Col>     
 <Col>
 <Button variant="primary" size="sm" onClick={()=>{setModal(!modal);setChange(grupo)}}>Editar</Button> {' '}
 <Button  variant="warning"size="sm"  onClick={()=>copy("grupoOpciones",grupo)}>Copiar</Button>{' '}
 <Button  variant="danger"size="sm"  onClick={()=>remove("grupoOpciones",ids,idp,grupo.idg)}>Borrar</Button>
 </Col>  
 <Button variant="light"  size="sm" onClick={()=>setOpciones(!mostrarOpciones)}>{mostrarOpciones? "Ocultar Opciones": "Mostrar Opciones"}</Button> 
 </Row>
 </div>
 <Modal show={modal} >
 <Modal.Header>
  <div><h3>Editar Registro</h3></div>
 </Modal.Header>
 <Modal.Body >
   <FormGroup>
     <label> Nombre Grupo de Opciones: </label>
     <input className="form-control"name="nombreGrupo"type="text"onChange={handleInput}placeholder={current.nombreGrupo}
     />
   </FormGroup>
   <FormGroup>
     <label> Cantidad Minima: </label>
     <input className="form-control"  name="cantidadMinima" type="number" min="0" onChange={handleInput} placeholder={current.cantidadMinima}
     />
   </FormGroup>
   <FormGroup>
     <label> Cantidad Maxima: </label>
     <input className="form-control"name="cantidadMaxima" type="number" min="0" onChange={handleInput}placeholder={current.cantidadMaxima}/>
   </FormGroup> 
 </Modal.Body>
 <Modal.Footer>
   <Button variant="success" onClick={() => {edit("grupoOpciones",current,ids,idp,grupo.idg);setModal(!modal)}}>Editar</Button>
   <Button variant="danger" onClick={() =>setModal(!modal)}>Cancelar </Button>
 </Modal.Footer>
</Modal>       

 {mostrarOpciones &&
(<Card.Body>
  {grupo.opciones.map((opcion)=> {return <Opcion  key={opcion.ido} opcion={opcion} ids={ids} idp={idp} idg={grupo.idg} remove={remove}/>})}
 <Form.Group style={{padding: "0.1rem"}}>
        <Row style={{padding: "0.25rem"}}>
        <Col><input type="text" onChange={handleNuevo} name="nombreOpcion" className="form-control" placeholder="Nombre de la Opcion"></input></Col>
        <Col><input type="number" min="0" onChange={handleNuevo} name="precioAdiconal" className="form-control" placeholder="Precio"></input></Col>
        </Row>
<Button  type="button" variant="success" onClick={()=>{handleSubmit(ids,idp,grupo.idg)}}>Agregar Nueva Opción</Button>
</Form.Group>
</Card.Body>
)}

</div>
)}
export default GrupoOpciones;