import React, { useState } from 'react';

const Formulario=({add})=>{
    const[nombreSeccion,setSeccion]=useState("")
    

   const handleInput=(e)=>{
    e.preventDefault()
    const {value} =e.target 
    setSeccion(value)
   }
   
   const handleSubmit=()=>{
    const id=Math.floor(Math.random() * 10000) + 1
    const nuevaseccion={ids:id,nombreSeccion:nombreSeccion,productos:[]}
    add("seccion",nuevaseccion)}
   

  return ( 
      <div className="card" >
       <form className="card-body" >
          <div className="form-group" >
           <input type="text" onChange={handleInput}   name="nombreSeccion" className="form-control" placeholder="Nombre de Sección"></input>
           <br></br>
          <button type="button" className="btn btn-primary" onClick={handleSubmit} >Agregar Nueva Seccion</button>
          </div>
          </form>
       </div> )
  }
export default Formulario;