import React, { useState, useRef } from 'react';

import ReactToPrint from 'react-to-print';
import {
    Container,
  } from "react-bootstrap";



const Printer = ({menu, nombreRestaurant}) => {
  const [show,setShow] = useState(false);
  const componentRef = useRef();
  return (
    
    <section> 
        <button className="btn btn-primary" onClick={()=>{setShow(!show)}}> Ver PDF</button>{' '}
    {show && 
    <div >
   <ReactToPrint
    trigger={() => <button >Descargar PDF</button>}
    content={() => componentRef.current}/>
      <Container ref={componentRef}
      style={{
        width: "210mm",
        minHeight: "297mm",
        margin: "10mm auto",
        background: "white",
        left: 0,
        textAlign: "center"}}>
        <br></br>
      <h3>{nombreRestaurant}</h3>
      <br></br>
      {menu.map((seccion)=> {return <List key={seccion.ids} seccion={seccion}/>})}
      </Container>
      </div>}
    </section>
  );
};

const List = ({ seccion }) => {
  return (
    <>
        <div>
        <h3 style={{
        backgroundColor:'#FA0050',
        color: 'white'}}>{seccion.nombreSeccion}</h3>
        </div>
      {seccion.productos.map((producto) => {
        return (
          <SingleProduct
            key={producto.idp}
            producto={producto}/> );
      })}
    </>
  );
};

const SingleProduct= ({ producto}) => {
  return (
    <div style={{padding: 5}}>
    <h5 style={{color: '#00d9fc'}}>{producto.nombreProducto} {'  '}{producto.precio}</h5>
      <p>{producto.descripcion}</p>
    </div>
  );
};

export default Printer;
