import React, { useState} from "react"
import GrupoOpciones from './GrupoOpcion.js'
import {
  Container,
  Button,
  Modal,
  Form,
  Row,
  Col
} from "react-bootstrap";



const Producto=({producto,add,edit,copy,paste,remove,ids})=>{
  
    const[modal,setModal]=useState(false)
    const[mostrarGrupos,setGrupos]=useState(false)
    const [current, setChange] = useState("");
    const [nuevo, setNuevo]=useState("")
    const handleNuevo=(e)=>{
      e.preventDefault()
      const {value, name} =e.target 
      if(value!==""){
      setNuevo({...nuevo, [name] : value})}}
    const handleInput=(e)=>{
        e.preventDefault()   
        const {value, name} =e.target 
        if(value!==""){
        setChange({...current, [name] : value})
    }}

    const handleSubmit=(ids,idp)=>{
      
      const id=Math.floor(Math.random() * 10000) + 1
      const nuevogrupo={idg:id,nombreGrupo: nuevo.nombreGrupo,cantidadMinima:nuevo.cantidadMinima,cantidadMaxima:nuevo.cantidadMaxima,modificaPrecio:0,opciones:[]}
      add("grupoOpciones",nuevogrupo,ids,idp) }
        
return(
<div>
<Container>
   <Row style={{padding: "0.2rem"}}> 
  <Col><h5>{producto.nombreProducto}</h5></Col> 
  <Col><h5>Precio: {producto.precio}</h5></Col>
  <Col>
  <Button variant="primary" size="sm" onClick={()=>{setModal(!modal);setChange(producto)}}>Editar</Button>{' '}
  <Button variant="danger" size="sm" onClick={()=>remove("producto",ids,producto.idp)}>Borrar</Button> </Col> 
  <p>Descripción: {producto.descripcion}</p>
  <Button variant="light" size="sm" onClick={()=>{setGrupos(!mostrarGrupos)}}>{mostrarGrupos? "Ocultar Grupos de Opciones": "Mostrar Grupos de Opciones"}</Button>  {' '}
  </Row >
<Modal show={modal} >
<Modal.Header><div><h3>Editar Registro</h3></div> </Modal.Header>
<Modal.Body ><Form.Group>
    <label>Nombre Producto: </label>
    <input className="form-control" name="nombreProducto" type="text" onChange={handleInput} placeholder={current.nombreProducto} defaultValue={current.nombreProducto}/>
  </Form.Group>
  <Form.Group>
    <label>Descripcion: </label>
    <input className="form-control"name="descripcion"type="text"onChange={handleInput} placeholder={current.descripcion} defaultValue={current.descripcion}/>
  </Form.Group>
  <Form.Group>
    <label>Precio: </label>
    <input className="form-control"name="precio" type="number" min="0"onChange={handleInput} placeholder={current.precio}defaultValue={current.precio} />
  </Form.Group>
</Modal.Body>
<Modal.Footer>
  <Button variant="success"onClick={() => {edit("producto",current,ids,current.idp);setModal(false)}}>Editar</Button>
  <Button variant="danger"onClick={() => setModal(false)}>Cancelar</Button>
</Modal.Footer>
</Modal>
{mostrarGrupos &&
<Container>
{producto.grupoOpciones.map((grupo)=> {return <GrupoOpciones key={grupo.idg} grupo={grupo} ids={ids} idp={producto.idp} remove={remove} add={add} edit={edit} copy={copy} paste={paste}/>})}
<Form.Group  style={{padding: "0.4rem"}}>
         <Row style={{padding: "0.25rem"}}>
          <input type="text" onChange={handleNuevo} name="nombreGrupo" className="form-control" placeholder="Nombre del Grupo de Opciones"></input>
          </Row>
          <Row style={{padding: "0.1rem"}}>
          <Col><input type="number" min="0" onChange={handleNuevo} name="cantidadMinima" className="form-control" placeholder="Cantidad Minima"></input></Col>
          <Col><input type="number" min="0" onChange={handleNuevo} name="cantidadMaxima" className="form-control" placeholder="Cantidad Maxima"></input></Col>
          </Row>
          <button  type="button" className="btn btn-success" onClick={()=>{handleSubmit(ids,producto.idp)}}>Agregar Nuevo Grupo de Opciones</button>{' '}
          <button  type="button" className="btn btn-warning" onClick={()=>{paste("grupoOpciones",ids,producto.idp)}}>Pegar Grupo de Opciones</button>     
</Form.Group>
</Container>}
</Container>
</div>)

}
export default Producto;