import React,{useState,useEffect} from "react"
import Navigation from './components/navigation.js'
import {menus} from './menu.json'
import Formulario from './components/Formulario.js'
import Seccion from './components/Seccion.js'
import Printer from './components/Printer.js'
import {Container,Button} from "react-bootstrap";
import ReactGa from "react-ga"

function App() {
  const [menu,setMenu]=useState(menus)
  const [nombreRestaurant,setRestaurant]=useState("Ingrese Nombre Restaurant")
  const [copy,setCopy]=useState([])
  
useEffect(() => {
  ReactGa.initialize("G-SY3ZL5E16J")
}, [])

 
  
  const handleAdd=(group,example,ids,idp,idg)=>{
    if(group==="seccion"){
      if(example.nombreSeccion!==""){
      setMenu([...menu,example])
      console.log(example)
    }else{
      alert('Debes cargar el nombre de la sección');
      return;}
  }else{if(group==="producto"){
      if(example.nombreProducto!==undefined & example.precio!==undefined & example.descripcion!==undefined){
      const nuevoMenu=[...menu]
      const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
      nuevoMenu[indexs].productos.push(example)
      setMenu(nuevoMenu)}else{
        alert('Debes cargar el nombre del producto, la descripción y el precio mismo');
        return;}
    }else{if(group==="grupoOpciones"){
      if(example.nombreGrupo!==undefined & example.cantidadMinima!==undefined & example.cantidadMaxima!==undefined ){
        if(example.cantidadMinima<=example.cantidadMaxima){
        const nuevoMenu=[...menu]
        const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
        const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)
        nuevoMenu[indexs].productos[indexp].grupoOpciones.push(example)
        setMenu(nuevoMenu)}else{alert("La Cantidad Minima no puede superar la Cantidad Maxima")}
    }else{
        alert('Debes cargar el nombre del grupo de Opciones (Ejemplo: "Extras"), la cantidad minima (puede ser 0 en caso de no ser obligatorio la seleccion de una opción) y la cantidad maxima (puede ser igual a la cantidad minima en caso que solo se pueda elejir una cantidad fija');
        return;}
    }else{if(group==="opcion"){
      if(example.nombreOpcion!==undefined & example.precioAdicional!==undefined){
        const nuevoMenu=[...menu]
        const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
        const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)
        const indexg=nuevoMenu[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)
        nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].opciones.push(example)
        setMenu(nuevoMenu)}else{
        alert('Debes cargar el nombre de la opción y el precio de la misma. (Puede ser 0 en caso no tener valor adicional))');
        return;}
  }
  }}}}
  const handleInput=(e)=>{
    e.preventDefault()
    const value= e.target.value
    if(value!==""){
    setRestaurant(value)}}

  const handleRemove=(group,ids,idp,idg,ido)=>{
    if(group==="seccion"){
      if( window.confirm("Cuidado al borrar la seccion, se borraran todos los productos que contiene.¿Seguro quieres borrar la Seccion?")){
        console.log(ids)
        const nuevoMenu=[...menu]
        const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
        nuevoMenu.splice(indexs, 1)
        setMenu(nuevoMenu)}
    
      }
    else{if(group==="producto"){
      const nuevoMenu=[...menu]
      const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
      const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)
      nuevoMenu[indexs].productos.splice(indexp, 1)
      setMenu(nuevoMenu)
      }else{if(group==="grupoOpciones"){
        const nuevoMenu=[...menu]
        const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
        const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)
        const indexg=nuevoMenu[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)
        nuevoMenu[indexs].productos[indexp].grupoOpciones.splice(indexg, 1)
        setMenu(nuevoMenu)
      }else{if(group==="opcion"){
        const nuevoMenu=[...menu]
        const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
        const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)
        const indexg=nuevoMenu[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)
        const indexo=nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].opciones.findIndex((item) => item.ido === ido)
        nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].opciones.splice(indexo, 1)
        setMenu(nuevoMenu)
    }else{if(group==="todo"){
      if( window.confirm("CUIDADO- ¿Seguro quieres borrar todos las secciones y todos los productos cargados? Comenzarás a cargar tu menu sin ninguna guia")){
        setMenu([])
      }}
  }}}}
  }
  const handleEdit=(group,example,ids,idp,idg,ido)=>{
    if(group==="seccion"){
      const nuevoMenu=[...menu]  
      const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
      nuevoMenu[indexs].nombreSeccion=example
      setMenu(nuevoMenu)
    }else{if(group==="producto"){
      const nuevoMenu=[...menu]  
      const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
      const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)  
      console.log(example)
      nuevoMenu[indexs].productos[indexp].nombreProducto=example.nombreProducto
      nuevoMenu[indexs].productos[indexp].descripcion=example.descripcion
      nuevoMenu[indexs].productos[indexp].precio=example.precio
      setMenu(nuevoMenu)
    }else{if(group==="grupoOpciones"){
      const nuevoMenu=[...menu]  
      const indexs =nuevoMenu.findIndex((item) => item.ids === ids)
      const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)
      const indexg=nuevoMenu[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)  
      nuevoMenu[indexs].nombreSeccion=example
      nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].nombreGrupo=example.nombreGrupo
      nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].cantidadMinima=example.cantidadMinima
      nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].cantidadMaxima=example.cantidadMaxima
      nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].modificaPrecio=example.modificaPrecio
      setMenu(nuevoMenu)
    }else{if(group==="opcion"){
      const nuevoMenu=[...menu]  
      const indexs = nuevoMenu.findIndex((item) => item.ids === ids)
      const indexp = nuevoMenu[indexs].productos.findIndex((item) => item.idp === idp)
      const indexg=nuevoMenu[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)  
      const indexo=nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].opciones.findIndex((item) => item.ido === ido)  
      nuevoMenu[indexs].productos[indexp].grupoOpciones[indexg].opciones[indexo]=example
      setMenu(nuevoMenu)
  }
  }}}}
  const handleCopy=(group,example)=>{
    if(group==="seccion"){
      setCopy({ copyS: example})}
    else{if(group==="producto"){
      setCopy({ copyP: example})}
    else{if(group==="grupoOpciones"){
      setCopy({ copyG: example})}
    else{if(group==="opcion"){
      setCopy({ copyO: example})}}}}}

const handlePaste=(group,ids,idp,idg)=>{
    if(group==="seccion"){
      if(typeof copy.copyS !== "undefined"){
        let example=copy.copyS
        example.ids=Math.floor(Math.random() * 10000) + 1
        handleAdd(group,example)
      }
    }else{if(group==="producto"){  
      if(typeof copy.copyP !== "undefined"){
        let example=copy.copyS
        example.ids=Math.floor(Math.random() * 10000) + 1
        handleAdd(group,example)
      }
    }else{if(group==="grupoOpciones"){
      if(typeof copy.copyG !== "undefined"){
        const grupo= JSON.parse(JSON.stringify(copy.copyG))
        const id=Math.floor(Math.random() * 10000) + 1
        const nuevogrupo={idg:id,nombreGrupo: grupo.nombreGrupo,cantidadMinima: grupo.cantidadMinima,cantidadMaxima:grupo.cantidadMaxima,modificaPrecio:grupo.modificaPrecio,opciones:grupo.opciones}
        handleAdd(group,nuevogrupo,ids,idp)
      }
    }}}}
  
  const downloadOptionals=()=> {
    const prueba= menu.map((seccion)=>{
    return [seccion.productos.map((producto)=>{return producto.idp}),seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.idg})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.nombreGrupo})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.cantidadMaxima})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.cantidadMinima})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.modificaPrecio})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.opciones.map((opciones)=>{return opciones.nombreOpcion})})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.opciones.map((opciones)=>{return opciones.precioAdicional})})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.opciones.map((opciones)=>{return opciones.ido})})})]
    })
    let productoi=[]
    for(let is=0;is<prueba.length;is++){
      for(let ip=0;ip<prueba[is][0].length;ip++){
        if(prueba[is][1][ip]!==[]){  
        for(let ig=0;ig<prueba[is][1][ip].length;ig++){
          if(prueba[is][1][ip][ig]!==[]){  
          for(let io=0;io<prueba[is][6][ip][ig].length;io++){
              let opcion=[prueba[is][0][ip],// SKU producto
              prueba[is][2][ip][ig],// Nombre del Grupo de Opcion
              "",// idg prueba[is][1][ip][ig]
              "",// Nombre del Grupo de Opcion prueba[is][2][ip][ig]
              prueba[is][4][ip][ig],// Cantidad Minimanpm 
              prueba[is][3][ip][ig],// Cantidad Maxima
              prueba[is][6][ip][ig][io],// Nombre Opcion
              prueba[is][7][ip][ig][io],// Precio Opcion
              prueba[is][8][ip][ig][io],// ido
              "",// Nombre Opcion prueba[is][6][ip][ig][io]
              prueba[is][5][ip][ig]]// ModificaPrecio
              productoi.push(opcion)}}
          }}}
  }
  if(productoi.length>0){let csvContent = "data:text/csv;charset=utf-8,";
  productoi.forEach(function(rowArray) {
    let row = rowArray.join(";");
    csvContent += row + "\r\n";
});
var encodedUri = encodeURI(csvContent);
var link = document.createElement("a");
link.setAttribute("href", encodedUri);
link.setAttribute("download",  nombreRestaurant+"Opcionales.csv");
document.body.appendChild(link); // Required for FF
link.click();
    }else{
      return;}}
  const downloadMenu=()=> {
  const prueba= menu.map((seccion)=>{
  return [seccion.nombreSeccion,seccion.productos.map((producto)=>{return producto.idp}),seccion.productos.map((producto)=>{return producto.nombreProducto}),seccion.productos.map((producto)=>{return producto.precio}),seccion.productos.map((producto)=>{return producto.descripcion})]
  })
  
  let productoi=[]
  for(let i=0;i<prueba.length;i++){
    for(let a=0;a<prueba[i][1].length;a++){
      if(prueba[i][3][a]>0){
      let productof=[prueba[i][0],// Nombre Seccion
      prueba[i][1][a],// IDP
      prueba[i][2][a],// Nombre Producto
      prueba[i][3][a],// Precio del Producto
      "",// Imagen del Producto
      prueba[i][4][a]// Descripcion Producto
      ,""// Unidad de Medida
      ,""// Cantidad de Unidades
      ,""// Impuesto
      ,""// Id del Producto en catalogo
      ,""// Indice
    ]
      productoi.push(productof)    }else{
        alert('Existen productos sin precio, por favor, carga el precio a todos tus productos e intenta nuevamente.');
        return;}
    }  
  }
 if(productoi.length>20){ let csvContent = "data:text/csv;charset=utf-8,";
  productoi.forEach(function(rowArray) {
    let row = rowArray.join(";");
    csvContent += row + "\r\n";
});
var encodedUri = encodeURI(csvContent);
var link = document.createElement("a");
link.setAttribute("href", encodedUri);
link.setAttribute("download", nombreRestaurant+" Productos.csv");
document.body.appendChild(link); // Required for FF
link.click();
downloadOptionals() 
ReactGa.event({
  category:"Descarga de Menu",
  action:"Descarga de Menu"
})
}else{
  alert('Debes cargar a lo sumo 20 productos');
  return;}
  }
  
   const downloadArchivo=()=> { 
        const jsonData=menu
        let dataStr = JSON.stringify(jsonData);
        let dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
        let exportFileDefaultName = nombreRestaurant;
        let linkElement = document.createElement('a');
        linkElement.setAttribute('href', dataUri);
        linkElement.setAttribute('download', exportFileDefaultName);
        linkElement.click();
        
        ReactGa.event({
  category:"Descarga de Avance",
  action:"Descarga de Avance"
})
      }
  const openFile=(evt)=> {
    const fileObj = evt.target.files[0];
    const reader = new FileReader();
    let fileloaded = e => {
    const fileContents = e.target.result;
    var myobj = JSON.parse([fileContents])
    setMenu(myobj)
    }
    fileloaded = fileloaded.bind(this);
    reader.onload = fileloaded;
    reader.readAsText(fileObj);  
      }
  return ( 
  <div  style={{textAlign: "center"}}  onSubmit={e => { e.preventDefault(); }} >
  <Navigation cantidadSecciones={menu.length} />
<Container>
<div style={{padding: "0.5rem"}}>
  <h6>Bienvenido al Generador Inicial de Menu, donde podras cargar todos tus productos.</h6>
  <h6>A modo de guia, pre-cargamos algunas secciones con los productos más comunes. Podes editar y borrar las propuestas según se ajusten a tu negocio. </h6>
  <h6>Descarga tu avance y volve a cargarlo para continuar cuando necesites.</h6> 
  <h6>Una vez que finalices, descarga el Menu y compartile los archivos a tu asesor.</h6>
  <br></br>
<input type="text" style={{textAlign: "center"}} name="nombreRestaurant" className="form-control" placeholder={nombreRestaurant} onChange={handleInput}></input>
</div>
<div className="d-grid gap-2">
<Button  variant="danger" size="sm" block onClick={()=>handleRemove("todo")}>Borrar Menu- Cargar sin Propuesta</Button>
</div>
<div style={{padding: "0.5rem"}}>


 {menu.map((seccion)=> {return <Seccion key={seccion.ids} seccion={seccion} add={handleAdd} remove={handleRemove} edit={handleEdit} copy={handleCopy} paste={handlePaste}/>})}
 <Formulario add={handleAdd}/>
  </div>
  </Container>
  <Container>
  <br></br>
  
  <form method="post" encType="multipart/form-data" >
  <label htmlFor="file"><h6>Continuar con un Avance Descargado</h6></label>{'   '}{' '}
  <input type="file" multiple={false}id="file" accept=".json" onChange={evt => openFile(evt)}/>
  </form>
  <button className="btn btn-warning" onClick={downloadArchivo}> Seguir Más Tarde. Descargar Avance</button>{' '}

  <br></br>
  <br></br>
  <button className="btn btn-success" onClick={downloadMenu}>¡Terminado! Descargar Menu</button>{' '}
  <br></br>
  <br></br>
  <Printer menu={menu} nombreRestaurant={nombreRestaurant}/>
    </Container>
</div>

  );
}

export default App;
